<template>
	<div class="layout" style="background: #f4f4f4">
		<div class="container">
			<div class="monkey-detail-area">
				<div class="title">{{detail.title}}
					<aside>{{detail.time || '一天前'}}发布</aside>
				</div>
				<div class="content" v-html="html"></div>
			</div>
		</div>
	</div>
</template>
<script>
import marked from 'marked'
export default {
	data() {
		return {
			detail: {},
			html: ''
		}
	},
	mounted() {
		this.$store.dispatch('article/getArticle', this.$route.params.id).then(d => {
			this.detail = d
			return this.$store.dispatch('article/getArticleDetail', d.content)
		}).then(res => {
			this.html = marked(res.data)
		})
	}
}
</script>
<style lang="stylus">
.monkey-detail-area
	width 1024px
	background #fff
	margin 20px auto
	.title
		font-size 24px
		font-font-weight 300
		padding 30px 30px 16px
		aside
			font-size 12px
			color #bbb
	.content
		color #666666
		padding 10px 0 30px
		>*
			padding 0 25px
		h1,h2,h3,h4,h5,h6
			margin-bottom .5rem
		h1
			background #f8f8f8
			padding 28px 25px
			font-size 16px
			margin-bottom 20px
		p
			line-height 1.6
			margin-bottom 10px
		img
			margin-bottom 10px
			max-width 100%
</style>





