import axios from 'axios'
export default{
  namespaced: true,
  state: {
    articles: []
  },
  mutations: {
    setArticles(state, list) {
      state.articles = list
    }
  },
  actions: {
    getArticles({commit}) {
      return axios.get('/mine/articles/list.json').then(res => {
        commit('setArticles', res.data)
        return res.data
      })
    },
    getArticle({state, commit, dispatch}, id) {
      if (!state.articles.length) {
        return dispatch('getArticles').then(res => {
          return res.find(d => d.id === +id)
        })
      }
      return Promise.resolve(state.articles.find(d => d.id === +id))
    },
    getArticleDetail({commit}, url) {
      return axios.get(url)
    }
  }
}



